/* kanbanboard.css */

/* Header */
.header {
    height: 60px;
    width: 100vw;
    background-color: white;
  }
  
  /* Body Content */
  .body-content {
    background-color: rgb(212, 218, 226);
    min-height: calc(100vh - 80px); /* To fill the remaining viewport height */
    width:150vw;
    
  }
  
  /* Display Button */
  .display-button {
    width: 120px;
    height: 40px;
    background-color: white;
    color: black;
    font-size: 14px;
    border: none;
    border-radius: 5px;
    border:2px solid gray;
    cursor: pointer;
    position: relative;
    display: flex;
    align-items: center;
    
    top:-70px;
    left:70px;
    justify-content: center;
  }
  .downicon{
    height: 30px;
    width: 30px;
    margin-right: 10px;

  }
  .display-button .display-icon {
    height: 17px;
    width: 25px;
    margin-right: 10px;
  }
  .kanban-icon{
    position:relative;
    left:220px;
    top:-45px;
  }
  .status-icon{
    position:relative;
    left:-30px;
    top:48px;
  }
  /* Options Card */
  .options-card {
    background-color:white;
    padding: 20px;
    position: relative;
    top: -60px; /* Adjust based on your layout */
    left: 50px; /* Adjust based on your layout */
    border: 1px solid #ddd;
    border-radius: 15px;
    z-index: 1;
    height:120px;
    width:400px;
  }
  
  .option-group {
    margin-bottom: 1px;
  }
  
  .option-group-label {
    display: block;
    position:relative;
   height:10px;
   left:0px;
   top:25px;
   color:rgb(87, 85, 85);

    margin-bottom: 5px;
  }
  
  .option-group-select {
    width: 30%;
    height:10px;
    padding: 15px;
    position:relative;
    left:240px;
  }
  
  /* Kanban Board */
  .kanban-container {
    position: relative;
  }
  
  .kanban-board {
    display: flex;
    flex-wrap: nowrap; /* Do not wrap columns */
    
    padding: 10px 0;
    margin-top: 20px;
  }
  
  .kanban-column {
    background-color:rgb(212, 218, 226);
   font-weight:normal;
   position:relative;
   left:100px;
   top:-60px;
   width:400px;
    border-radius: 5px;
    padding: 10px;
    min-width: 300px;
    margin-right: 10px; /* Space between columns */
    flex: 0 0 auto; /* Prevent columns from shrinking */
    box-sizing: border-box;
  }
  
  .kanban-column h2 {
    font-weight:normal;
   
  }
  .priority-icon{
    position:relative;
    left:-30px;
    top:50px;

  }
  
  /* Adjustments for responsiveness */
  @media (max-width: 900px) {
    .kanban-board {
      flex-wrap: wrap; /* Allow columns to wrap on smaller screens */
    }
  
    .kanban-column {
      min-width: 100%;
      margin-right: 0;
      margin-bottom: 20px;
    }
  }
  