/* ticketcard.css */

.ticket-card {
    background-color: #fff;
    border: 1px solid #ddd;
  
    border-radius: 18px;
    border-top-right-radius: 18px;
    border-bottom-right-radius: 18px;
    padding: 10px;
    margin-bottom: 10px;
    box-sizing: border-box;
    width:400px;
    box-shadow: 2px 2px 3px outlet gray;
    height:150px;
    opacity: 0;
    animation: fadeIn 2s forwards;
  }
  @keyframes fadeIn {
    to {
      opacity: 1;
    }
  }
  /* Priority Colors */
  .ticket-card.priority-urgent {
    border-left-color: #e74c3c; /* Red */
  }
  
  .ticket-card.priority-high {
    border-left-color: #e67e22; /* Orange */
  }
  
  .ticket-card.priority-medium {
    border-left-color: #f1c40f; /* Yellow */
  }
  
  .ticket-card.priority-low {
    border-left-color: #2ecc71; /* Green */
  }
  
  .ticket-card.priority-no-priority {
    border-left-color: #95a5a6; /* Gray */
  }
  /* TicketCard.css */

/* Existing styles for .ticket-card, .ticket-card h3, .priority-image, etc. */

/* Feature Request Label */
.feature-request-label {
    border: 1px solid #ccc; /* Add a border */
    padding: 5px;
    margin: 10px 0; /* Space above and below the label */
    text-align: center;
    font-weight: bold;
    font-size: 14px;
    height:30px;
    width:180px;
    border-radius:5px;

  }
  
  /* Radio Button Container */
  .radio-button-container {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
  }
  
  .radio-button {
    transform: scale(1.2); /* Increase the size of the radio button */
    position:relative;
    top:-45px;
    left:-170px;
  }
  
  /* Ticket ID */
  .ticket-id {
    text-align: center;
    font-size: 12px;
    color: #555;
    position:relative;
    top:-200px;
    left:-200px;
  }
  h3{
    font-weight:normal;
    font-size:15px;
  }
  
  